.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #FFA500;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.front-image{
    width: 100%;
    height: 35rem;
    overflow: hidden;
}
.front-image img{
   width: 110%;
   height: 100%;
   object-fit: cover;
   transform: rotateZ(-4deg) translateY(-5rem) translateX(-1rem);
}